import * as React from 'react'
import './chart.scss'
import * as d3 from 'd3'
import { useRef } from 'react'
import { cyclingData } from './data'

export interface ChartProps {
    data?: any
}

interface PlatformColors {
    key1: string
    key2: string
    key3: string
    key4: string
    key5: string
    key6: string
    key7: string
    key8: string
    key9: string
    key10: string
    key11: string
    key12: string
    key13: string
    key14: string
    key15: string
    key16: string
    key17: string
    key18: string
}

const Chart: React.FC<ChartProps> = props => {
    const d3Container = useRef(null)

    const data = cyclingData
    console.log('data', data)
    const tooltip = d3
        .select('body')
        .append('svg')
        .attr('id', 'tooltip')
        .style('background-color', '#fff')
        .style('position', 'absolute')
        .style('visibility', 'hidden')
        .style('color', '#ccc')
        .style('padding', '3px')
        .style('border', '2px solid black')
        .style('font-family', 'Arial')
        .style('text-align', 'center')

    const consoleName = d3
        .select('body')
        .append('div')
        .attr('id', 'consoleName')
    const gameName = d3
        .select('body')
        .append('div')
        .attr('id', 'gameName')
    const copiesSold = d3
        .select('body')
        .append('div')
        .attr('id', 'copiesSold')

    const width = 960
    const height = 600
    const legendItems: any = []

    const settings = d3
        .treemap()
        .tile(d3.treemapResquarify)
        .size([width, height])
        .round(true)
        .paddingInner(1)
        .paddingOuter(1)

    const root = d3
        .hierarchy(data)
        .eachBefore((d: any) => {
            d.data.id = (d.parent ? d.parent.data.id + '.' : '') + d.data.name
            if (d.parent && d.children) {
                d.data.hue = Math.floor(Math.random() * 361)
                d.data.saturation = Math.floor(Math.random() * 70) + 25
                d.data.lightness = Math.floor(Math.random() * 40) + 55
                legendItems.push(d)
            }
        })
        .sum((d: any) => d.value)
        .sort((a: any, b: any) => {
            return b.height - a.height || b.value - a.value
        })

    settings(root)

    const treemap = d3
        .select('body')
        .append('svg')
        .attr('id', 'title')
        .attr('width', width)
        .attr('height', height)
        .style('margin', '10px')
        .style('background-color', '#000')
        .style('border', '1px solid black')

    const cell = treemap
        .selectAll('g')
        .data(root.leaves())
        .enter()
        .append('g')
        .attr('transform', (d: any) => 'translate(' + d.x0 + ',' + d.y0 + ')')
        .attr('class', 'console')
        .attr('fill', 'black')
        .on('mousemove', (d: any) => {
            const { name, category, value } = d.data
            const [x, y] = [d3.event.pageX, d3.event.pageY]

            tooltip
                .style('visibility', 'visible')
                .style('top', y + 'px')
                .style('left', x + 20 + 'px')
                .attr('data-value', d.data.value)

            gameName.html(name)
            consoleName.html(`Console: ${category}`)
            copiesSold.html(`Copies Sold: ${value} million`)
            legendText.style('font-size', '16px')
        })
        .on('mouseout', () => {
            tooltip.style('visibility', 'hidden')
        })

    cell.append('rect')
        .attr('class', 'tile')
        .attr('id', (d: any) => d.data.id)
        .attr('width', (d: any) => d.x1 - d.x0)
        .attr('height', (d: any) => d.y1 - d.y0)
        .attr(
            'fill',
            (d: any) =>
                `hsl(${d.parent.data.hue}, ${d.parent.data.saturation}%, ${d.parent.data.lightness}%)`
        )
        .attr('data-name', (d: any) => d.data.name)
        .attr('data-category', (d: any) => d.data.category)
        .attr('data-value', (d: any) => d.data.value)

    cell.append('foreignObject')
        .attr('width', (d: any) => d.x1 - d.x0)
        .attr('height', (d: any) => d.y1 - d.y0)
        .append('xhtml:p')
        .attr('width', (d: any) => d.x1 - d.x0)
        .attr('min-height', (d: any) => d.y1 - d.y0)
        .style('margin-top', '3px')
        .style('font-family', 'Arial')
        .style('font-size', '12px')
        .text(d => d.data.name)

    /*LEGEND*/
    const legend = d3
        .select('body')
        .append('svg')
        .attr('id', 'legend')
        .attr('width', 1000)
        .attr('height', 590)
        .style('margin', '10px')
        .style('padding', '3px')
        .style('background-color', '#fff')
        .style('border', '1px solid black')

    const legends = legend
        .selectAll('g')
        .data(legendItems)
        .enter()
        .append('g')

    legends
        .append('rect')
        .attr('class', 'legend-item')
        .attr('width', 40)
        .attr('height', 25)
        .attr('x', 0)
        .attr('y', (d, i) => i * 32.5)
        .attr('fill', (d: any) => `hsl(${d.data.hue}, ${d.data.saturation}%, ${d.data.lightness}%)`)

    const legendText = legends
        .selectAll('text')
        .data(legendItems)
        .enter()
        .append('text')
        .attr('x', 48)
        .attr('y', (d, i) => i * 32.5 + 18)
        .style('fill', '#CCC')
        .style('font-family', 'Arial')
        .text((d: any) => d.data.name)

    return (
        <div id="description" ref={d3Container}>
            Top 100 Games
        </div>
    )
}

export default Chart
